<template>
  <div>
	<div  style="background-color:#1c272b;height:20vh;color:#ffffff!important">
        <div style="margin:20px"> 
        <p><small>Hosted with ♥ on <a  style = "color:#95DC85" href="https://github.com/sjljrvis">Github</a></small></p>
        <h5> Developed and Maintained by <a style="color:#95DC85 " href="https://github.com/sjljrvis">Sejal Chougule</a></h5>
        </div>
	</div>    
  </div>
</template>

<script>
export default {
  methods: {
    pushTo(route) {
      this.$router.push({
        path: `/${route}`,
        name: route
      });
    }
  }
};
</script>



