<template>
  <div>
       <div style="background-color:#1c272b;height:60vh;color:#ffffff!important">
             <div>
               <md-tabs class="md-accent" md-alignment="centered">
                <md-tab id="tab-home" md-label="Home" @click="pushTo('home')"></md-tab>
                <md-tab id="tab-skill" md-label="skill" @click="pushTo('skill')"></md-tab>
                <md-tab id="tab-projects" md-label="projects" @click="pushTo('projects')"></md-tab>
                <md-tab id="tab-resume" md-label="resume" @click="pushTo('resume')"></md-tab>
                <md-tab id="tab-hire" md-label="hire" @click="pushTo('hire')"></md-tab>                
              </md-tabs>
             </div>    
         <div> 
                <div style="margin:35px">
                    <h1>Sejal Chougule</h1>
                    <div style="font-size: 18px; font-weight: 400;color: #fff;margin: 20px 0 40px 0;">
                        <p>Full Stack Engineer
                        </p>
                        <p style = "color:#95DC85">
                         >  console.log(' Hello world ! ');<br>
                         >  console.log(' ♥ tech ');<br>
                         >  console.log(' building web ');
                        </p>
                    </div>
                </div>
         </div>
              
        </div> 
  </div>
</template>

<script>
export default {
  methods: {
    pushTo(route) {
      this.$router.push({
        path: `/${route}`,
        name: route
      });
    }
  }
};
</script>



