import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);

//register all routes here.
export default new VueRouter({
	base: __dirname,
	routes: [
		{ 
			name : 'home', 
			path: '*' ,
			component: require('../components/home.vue').default
        },
        { 
			name : 'skill', 
			path: '/skill' ,
			component: require('../components/skill.vue').default
        },
        { 
			name : 'projects', 
			path: '/projects' ,
			component: require('../components/projects.vue').default
		},
		{ 
			name : 'resume', 
			path: '/resume' ,
			component: require('../components/resume.vue').default
		},
		{ 
			name : 'hire', 
			path: '/hire' ,
			component: require('../components/hire.vue').default
        }
	]
});




